@import 'fonts/PT-Root-UI/font.scss';

// Общие стили

* {
	box-sizing: border-box;
}

html, body {
	padding: 0;
	margin: 0;
	height: 100%;
}

body {
	font-family: 'PT-Root-UI', sans-serif;
	background-color: #595D7E;
}

a {
	text-decoration: none;
}

img {
	max-width: 100%;
}

input {
	outline: none;
	border: none;
}

button {
	font-family: 'PT-Root-UI', sans-serif;
	outline: none;
	border: none;
}

button:hover {
	cursor: pointer;
}

#root {
	display: grid;
	grid-template-areas: 'header header'
	                     'main main';
	grid-template-rows: max-content;
	height: 100vh;
}