// Кнопки

.btn {
    color: #FFFFFF;
    border-radius: 4px;

    &_dark {
        background-color: #575A7B;

        &:hover {
            background-color: #464863;
        }

        &:active {
            background-color: #2A2C3C;
        }
    }
}

.advanced-btn {
    display: flex;
    padding: 4px;
    background-color: transparent;
    border-radius: 4px;

    &__text {
        font-size: 12px;
        margin-right: 3.5px;
    }

    &.on-light {
        .btn__text {
            color: #82859E;
        }

        .btn__icon path {
            fill: #82859E;
        }

        &:hover {
            background-color: #F3F3F7;
        }

        &:active {
            background-color: #D4D4E2;
        }
    }

    &.on-dark {
        .btn__text {
            color: #82859E;
        }

        .btn__icon path {
            fill: #82859E;
        }

        &:hover {
            background: rgba(243, 243, 247, 0.1);

            .btn__text {
                color: #B2B4C3;
            }
    
            .btn__icon path {
                fill: #B2B4C3;
            }
        }

        &:active {
            background: rgba(255, 255, 255, 0.5);

            .btn__text {
                color: #EEEFF2;
            }
    
            .btn__icon path {
                fill: #EEEFF2;
            }
        }
    }
}

.icon-btn {
    display: flex;
    padding: 5px;
    background-color: transparent;

    .btn__icon {
        pointer-events: none;
    }

    &.on-light {
        .btn__icon path {
            fill: #BEBFCC;
        }

        &:hover {
            background-color: #F2F2F5;

            .btn__icon path {
                fill: #A9ABBC;
            }
        }

        &:active {
            background-color: #D7D7E0;

            .btn__icon path {
                fill: #70728F;
            }
        }
    }

    &.on-dark {
        .btn__icon path {
            fill: #6D708E;
        }

        &:hover {
            background-color: #666A87;

            .btn__icon path {
                fill: #9D9FB4;
            }
        }

        &:active {
            background-color: #7F839E;

            .btn__icon path {
                fill: #D7D7E0;
            }
        }
    }
}