// Элемент секции

.section-item {
    display: grid;
    grid-auto-flow: column;
    grid-gap: 0 6px;

    &__icon {
        display: grid;
        align-content: center;
        justify-content: center;

        path {
            fill: #A5A8C0;
        }
    }

    &__text {
        color: #A5A8C0;
    }

    &.section-item_active {
        .text {
            color: #595D7E;
        }

        .icon path {
            fill: #595D7E;
        }
    }

    &:hover {
        .text {
            color: #595D7E;
        }

        .icon path {
            fill: #595D7E;
        }
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .section-item {
        &__text {
            font-size: 16px;
            font-weight: 500;
        }
    }
}

