// Моя литература

.my-literature {
    display: grid;
    grid-area: my-literature;
    padding: 30px 10px 24px 40px;
    overflow: hidden;
    transition: transform 0.3s;

    &__empty {
        display: grid;
        grid-template-columns: 388px;
        grid-auto-rows: max-content;
        align-items: center;
        justify-content: center;
        align-self: center;
        justify-self: center;
        text-align: center;

        &-title {
            color: #FFFFFF;
            font-size: 18px;
        }

        &-text {
            margin-top: 20px;
            padding: 22px 44px;
            color: #C6C7D2;
            font-size: 18px;
            line-height: 23px;
            border-radius: 8px;
            background-color: #787B96;
        }

        &-img {
            margin: 32px auto auto auto;
        }
    }

    &__build {
        display: grid;
        height: max-content;
        margin: 12px 24px 0 0;
        padding: 20px;
        border-radius: 8px;
        background-color: #787B96;

        &-title {
            max-width: 60%;
            color: #DEDFE5;
            font-size: 14px;
        }

        &-buttons-container {
            display: flex;
            flex-wrap: wrap;
            margin-top: 18px;

            .link-btn {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 45%;
                margin-right: 40px;
                padding: 13px;
                background-color: #F3F3F7;

                .btn__text {
                    display: inline-block;
                    width: 70%;
                    color: #595D7E;
                    font-size: 14px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-align: left;
                    text-overflow: ellipsis;
                }

                .btn__icon path {
                    fill: #BEBFCC;
                }

                &:hover {
                    .btn__icon path {
                        fill: #575B7B;
                    }
                }
            }

            .option-btn {
                padding: 13px 58px;
                margin-right: 13px;
                border-radius: 8px;
                background-color: #F3F3F7;
                
                .btn__text {
                    color: #595D7E;
                    font-size: 14px;
                    font-weight: 700;
                }
            }
        }
    }

    .list {
        grid-auto-rows: min-content min-content auto;

        .title {
            padding-right: 24px;
        }

        .items {
            margin-top: 24px;
        }
    }
}

@media (min-width:  1401px) and (max-width: 1880px) {
    .my-literature {
        &__build {
            &-title {
                max-width: 100%;
            }

            &-buttons-container {
                margin-top: 10px;
            }

            .link-btn {
                width: 80%;
                margin-right: 0;
            }

            .option-btn {
                margin: 10px 10px 0 0;
            }
        }
    }
}

@media (min-width:  1024px) and (max-width: 1400px) {
    .my-literature {
        grid-template-rows: auto auto;
        padding: 16px 20px;

        &__empty {
            grid-template-columns: minmax(min-content, 380px);

            &-text {
                padding: 18px 40px;
                margin-top: 16px;
                font-size: 15px;
                line-height: 18px;
            }

            &-img {
                width: 300px;
            }
        }

        &__build {
            padding: 10px;
            margin: 12px 24px 0 0;

            &-title {
                max-width: 100%;
            }

            &-buttons-container {
                margin-top: 10px;
            }

            .link-btn {
                width: 100%;
                margin-right: 0;
            }

            .option-btn {
                margin: 10px 10px 0 0;
            }
        }

        .list {
            .items {
                .item {
                    .text {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

@media (min-width:  768px) and (max-width: 1023px) {
    .my-literature {
        grid-template-rows: auto auto;
        padding: 16px 20px;

        &__empty {
            grid-template-columns: minmax(min-content, 380px);

            &-text {
                padding: 18px 40px;
                margin-top: 16px;
                font-size: 15px;
                line-height: 18px;
            }

            &-img {
                width: 300px;
            }
        }

        &__build {
            padding: 10px;
            margin: 12px 0 0 0;

            &-title {
                max-width: 100%;
                font-size: 13px;
            }

            &-buttons-container {
                margin-top: 10px;
            }

            .link-btn {
                width: 100%;
                margin-right: 0;
            }

            .option-btn {
                display: none;
            }
        }

        .list {
            .title {
                padding: 0;
            }

            .items {
                padding: 0;

                &::-webkit-scrollbar {
                    display: none;
                }

                .item {
                    .text {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .my-literature {
        grid-template-rows: auto auto;
        padding: 16px 20px;

        &__empty {
            grid-template-columns: auto;
            margin-top: 56px;

            &-text {
                padding: 18px 40px;
                margin-top: 16px;
                font-size: 14px;
                line-height: 18px;
            }

            &-img {
                width: 276px;
                height: 206px;
            }
        }

        &__build {
            padding: 10px;
            margin: 12px 0 0 0;

            &-title {
                max-width: 100%;
                font-size: 12px;
            }

            &-buttons-container {
                margin-top: 10px;
            }

            .link-btn {
                width: 100%;
                margin-right: 0;
            }

            .option-btn {
                display: none;
            }
        }

        .list {
            .title {
                padding: 0;
            }

            .items {
                padding: 0;

                &::-webkit-scrollbar {
                    display: none;
                }

                .item {
                    .text {
                        font-size: 13px;
                    }
                }
            }
        }
    }
}