// Переключатель разделов

.mobile-tab-switch {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 20px;
    grid-auto-flow: column;
    grid-template-columns: repeat(auto-fill, 1fr);
    width: max-content;
    padding: 4px;
    margin: 0 auto;
    background: #686D92;
    box-shadow: 0px 4px 16px rgba(45, 47, 68, 0.24);
    border-radius: 26px;
    transition: width 0.3s;

    &__tab {
        padding: 10px 12px;
        color: #FFFFFF;
        font-size: 13px;
        font-weight: 700;
        text-align: center;
        transition: color 0.3s;

        &-text {
            z-index: 2;
        }

        &_active {
            color: #575B7B;
        }
    }

    &__indicator {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        right: 4px;
        bottom: 4px;
        width: 0;
        height: calc(100% - 8px);
        border-radius: 26px;
        background-color: #FFFFFF;
        z-index: -1;
        transition: all 0.3s;
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .mobile-tab-switch {
        display: grid;
    }
}