// Литература

.literature {
    display: grid;
    grid-area: literature;
    grid-template-rows: min-content auto;
    min-height: 0;
    padding: 24px 10px 24px 40px;
    background-color: #FFFFFF;
    border-radius: 0px 40px 0px 0px;
    overflow: hidden;
    transition: transform 0.3s;

    &__sections {
        display: grid;
        grid-auto-flow: column;
        grid-auto-columns: max-content; 
        grid-auto-rows: min-content;
        grid-gap: 0 26px;
    }

    &__container {
        display: grid;
        grid-template-rows: min-content min-content auto;
        min-height: 0;
        margin-top: 24px;

        &-title {
            max-width: 60%;
            padding-right: 30px;
            color: #585C7D;
            font-size: 12px;
            line-height: 15px;
        }
    }

    &__empty {
        display: grid;
        grid-template-columns: 360px;
        align-content: center;
        justify-content: center;

        &-img {
            margin: auto;
        }

        &-text {
            margin-top: 33px;
            color: #C6C7D2;
            font-size: 18px;
            line-height: 23px;
            text-align: center;
        }
    }

    .list {
        margin-top: 32px;
        grid-auto-rows: min-content auto;

        .title {
            padding-right: 30px;
        }
    }

    .search-form {
        margin-top: 12px;
        padding-right: 30px;

        .input {
            &__container {
                &_size {
                    &_sm {
                        grid-column: span 1;
                    }

                    &_lg {
                        grid-column: span 5;
                    }
                }

                .btn {
                    margin: 4px;
                    padding: 9px 18px;
                }
            }

            &__title {
                margin-bottom: 8px;
            }
        }
    }
}

@media (min-width: 1024px) and (max-width: 1400px) {
    .literature {
        padding: 16px 20px;
        
        &__sections {
            grid-gap: 0 17px;
        }

        &__container {
            grid-template-rows: min-content min-content auto;
            margin-top: 17px;

            &-title {
                max-width: 80%;
            }
        }

        &__empty {
            grid-template-columns: 300px;
            padding: 0 34px;

            &-img {
                width: 250px;
            }

            &-text {
                margin-top: 15px;
                font-size: 15px;
            }
        }

        .list {
            .items {
                .item {
                    .text {
                        font-size: 14px;
                    }
                }
            }
        }

        .search-form {
            .inputs-group {
                grid-template-columns: auto;
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .literature {
        padding: 16px 20px;
        
        &__sections {
            grid-gap: 0 17px;
        }

        &__container {
            grid-template-rows: min-content auto;
            margin-top: 17px;

            &-title {
                display: none;
            }
        }

        &__empty {
            grid-template-columns: auto;
            padding: 0 34px;

            &-img {
                width: 200px;
            }

            &-text {
                margin-top: 15px;
                font-size: 15px;
            }
        }

        .list {
            .title {
                padding: 0;
            }

            .items {
                padding: 0;

                &::-webkit-scrollbar {
                    display: none;
                }

                .item {
                    .text {
                        font-size: 14px;
                    }
                }
            }
        }

        .search-form {
            margin-top: 0;
            padding: 0;

            .inputs-group {
                grid-template-columns: auto;
            }

            .input {
                &__container {
                    &_size {
                        &_sm {
                            grid-column: span 2;
                        }
    
                        &_lg {
                            grid-column: span 4;
                        }
                    }

                    .btn {
                        margin: 4px;
                        padding: 9px 18px;
                    }
                }
    
                &__title {
                    margin-bottom: 8px;
                }
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .literature {
        padding: 16px 20px;
        border-radius: 20px 20px 0px 0px;
        
        &__sections {
            grid-gap: 0 17px;
        }

        &__container {
            grid-template-rows: min-content auto;
            margin-top: 17px;

            &-title {
                display: none;
            }
        }

        &__empty {
            grid-template-columns: auto;
            padding: 0 34px;

            &-img {
                width: 136px;
                height: 98px;
            }

            &-text {
                margin-top: 8px;
                font-size: 13px;
            }
        }

        .list {
            .title {
                padding: 0;
            }

            .items {
                padding: 0;

                &::-webkit-scrollbar {
                    display: none;
                }

                .item {
                    .text {
                        font-size: 13px;
                    }
                }
            }
        }

        .search-form {
            margin-top: 0;
            padding: 0;

            .inputs-group {
                grid-template-columns: auto;
            }

            .input {
                &__container {
                    &_size {
                        &_sm {
                            grid-column: span 2;
                        }
    
                        &_lg {
                            grid-column: span 4;
                        }
                    }

                    .btn {
                        margin: 4px;
                        padding: 9px 18px;
                    }
                }
    
                &__title {
                    margin-bottom: 8px;
                }
            }
        }
    }
}