// Стили контента

.main {
    display: grid;
    grid-area: main;
    grid-template-areas: 'literature my-literature';
    grid-template-columns: 1fr 1fr;
    min-height: 0;
}

@media (min-width: 320px) and (max-width: 767px) {
    .main {
        grid-template-columns: 100% 100%;
        overflow: hidden;
    }
}