 @font-face {
	font-family: 'PT-Root-UI';
	src: url('PT-Root-UI-Regular.eot');
	src:
	     url('PT-Root-UI-Regular.eot?#iefix') format('embedded-opentype'),
	     url('PT-Root-UI-Regular.woff2') format('woff2'),
	     url('PT-Root-UI-Regular.woff') format('woff'),
	     url('PT-Root-UI-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}