// Поля ввода

.inputs-group {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-gap: 12px 16px;
    min-width: 0;
}

.input {
    &__container {
        display: grid;
        min-width: 0;

        &_size {
            &_sm {
                .input {
                    padding: 8px;
                }
            }
    
            &_lg {
                .input {
                    padding: 13px 16px;
                }
            }
        }

        &_error {
            .field {
                border: 2px solid rgba(255, 102, 102, 0.25);
            }
    
            .text {
                color: #FF6666;
            }
        }

        &_active {
            .field {
                border: 1px solid #E7E8EE;
                border-radius: 8px 8px 0px 0px;
                background-color: #F3F3F7;

                .select {
                    display: flex;
                    flex-direction: column;
                }
            }
        }
    }

    &__field {
        display: flex;
        position: relative;
        border: 2px solid transparent;
        border-radius: 8px;
        background-color: #F3F3F7;

        &-input {
            width: 100%;
            color: #575B7B;
            font-size: 14px;
            background-color: transparent;

            &::placeholder {
                color: #BEBFCC;
            }
        }

        &-select {
            display: none;
            position: absolute;
            top: 100%;
            left: -1px;
            padding: 12px 0 0 0;
            width: calc(100% + 2px);
            max-height: 250px;
            border: 1px solid #E7E8EE;
            border-radius: 0 0 8px 8px;
            background-color: #F9F9FB;
            z-index: 2;
            overflow-y: auto;

            &-title {
                padding: 0 12px;
                color: #989AAE;
                font-size: 11px;
            }

            &-item {
                display: flex;
                align-items: center;
                padding: 6px 12px;

                &-text {
                    display: inline-block;
                    padding: 4px;
                    color: #575B7B;
                    font-size: 13px;
                    border-radius: 3px;
                    background-color: #E7E8EE;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }

                &_active {
                    background-color: #F3F3F7;
                }

                &:hover {
                    cursor: pointer;
                    background-color: #F3F3F7;
                }
            }

            &::-webkit-scrollbar {
                width: 6px;
            }
            
            &::-webkit-scrollbar-thumb {
                border-top: 1.5px solid rgba(0, 0, 0, 0);
                border-bottom: 6px solid rgba(0, 0, 0, 0);
                border-left: 2px solid rgba(0, 0, 0, 0);
                border-right: 2px solid rgba(0, 0, 0, 0);
                border-radius: 5px;
                background-color: #C2C2D6;
                background-clip: padding-box;
            }
        }

        &:hover {
            background-color: #E7E8EE;
        }
    }

    &__title {
        color: #595D7E;
        font-size: 14px;
        font-weight: 700;
    }

    &__text {
        padding: 3px 8px 0 8px;
        font-size: 11px;
    }

    &__btn {
        &-text {
            font-size: 14px;
        }
    }
}