// Шапка

.header {
	display: grid;
	grid-area: header;
	grid-auto-flow: column;
	grid-auto-columns: max-content;
	justify-content: space-between;
	padding: 10px 40px;

	&__info {
		display: grid;
		grid-gap: 10px 0;

		&-desc {
			color: rgba(255, 255, 255, 0.33);
			font-size: 12px;
		}

		a {
			display: grid;
			align-content: center;
			width: max-content;
		}
	}

	&__creators {
		display: grid;
		align-content: start;

		&-text {
			margin-right: 3px;
			color: #9092A8;
			font-size: 12px;
		}

		&-link {
			&:hover {
				.text {
					color: #C6C7D2;
				}

				.logo path {
					fill: #C6C7D2;
				}
			}
		}
	}
}

@media (min-width:  768px) and (max-width: 1023px) {
	.header {
		padding: 30px 20px 10px 20px;

		&__info-desc {
			font-size: 11px;
		}

		&__creators-text {
			font-size: 11px;
		}
	}
}


@media (min-width: 320px) and (max-width: 767px) {
	.header {
		padding: 30px 20px 10px 20px;

		&__info-desc, &__creators-text {
			display: none;
		}
	}
}
