// Список книг

.list {
    display: grid;
    min-height: 0;

    &__title {
        display: grid;
        grid-auto-flow: column;
        justify-content: space-between;

        &-text {
            display: grid;
            align-content: center;
            font-size: 18px;
            font-weight: 700;
        }

        &-buttons-container {
            display: grid;
            grid-auto-flow: column;
            grid-gap: 0 10px;
        }
    }

    &__items {
        display: grid;
        grid-auto-rows: min-content;
        min-height: 0;
        margin-top: 12px;
        padding-right: 24px;
        overflow-y: auto;

        &-item {
            display: grid;
            grid-auto-flow: column;
            grid-template-rows: min-content;
            align-items: center;
            justify-content: space-between;
            padding: 16px 0;

            &-text {
                font-size: 15px;
            }

            .buttons__container {
                display: grid;
                grid-auto-flow: column;
                grid-gap: 0 8px;
            }
        }
    }

    &.on-light {
        .title {
            .text {
                color: #595D7E;
            }
        }

        .items {
            &::-webkit-scrollbar {
                width: 6px;
            }
            
            &::-webkit-scrollbar-thumb {
                border-radius: 2px;
                background-color: #C2C2D6;
            }
    
            &::-webkit-scrollbar-track {
                border-radius: 2px;
                background-color: #F3F3F7;
            }

            .item {
                border-bottom: 1px solid #F3F3F7;

                &:hover {
                    cursor: pointer;
                    background-color: #F9F9FB;
                }

                &:first-of-type {
                    border-top: 1px solid #F3F3F7;
                }

                .text {
                    color: #575B7B;
                }

                &_active {
                    background-color: #F9F9FB;
                }
            }
        }
    }

    &.on-dark {
        .title {
            .text {
                color: #FFFFFF;
            }
        }

        .items {
            &::-webkit-scrollbar {
                width: 6px;
            }
            
            &::-webkit-scrollbar-thumb {
                border-radius: 2px;
                background-color: #9697B0;
            }
    
            &::-webkit-scrollbar-track {
                border-radius: 2px;
                background-color: #696C8A;
            }

            .item {
                border-bottom: 1px solid #787B96;

                &:hover {
                    cursor: pointer;
                    background-color: #636786;
                }

                &:first-of-type {
                    border-top: 1px solid #787B96;
                }

                .text {
                    color: #DEDFE5;
                }

                &_active {
                    background-color: #636786;
                }
            }
        }
    }
}